* {
    box-sizing: border-box;
    font-family: 'Dosis', sans-serif;
    transition: all 2.5s ease;
}

:root{
    --height: 3em;
    --bgr: rgb(64, 64, 64);
}

.AlternativeSuperButton_btn__3Iu-0{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    cursor: pointer;
    color: white;
    background: rgb(64, 64, 64);
    background: var(--bgr);
    height: calc(1 * 3em);
    height: calc(1 * var(--height));
    width: calc(2 * 0.866 * 3em);
    width: calc(2 * 0.866 * var(--height));
    box-shadow: 0.5em 0.5em 0.5em lightgrey;
    transition: box-shadow ease 0.2s;
    z-index: 9;
}

.AlternativeSuperButton_btn__3Iu-0:after,
.AlternativeSuperButton_btn__3Iu-0:before{
    content: '';
    position: absolute;
    transition: all ease-in-out 0.35s;
    top: 0px; left: 0px;
    height: calc(1 * 3em);
    height: calc(1 * var(--height));
    width: calc(2 * 0.866 * 3em);
    width: calc(2 * 0.866 * var(--height));
    background: rgb(64, 64, 64);
    background: var(--bgr);
    opacity: 0;
    z-index: -5;
}

.AlternativeSuperButton_btn__3Iu-0:hover{
    transition: box-shadow linear 0.2s;
    box-shadow: none;
}
.AlternativeSuperButton_btn__3Iu-0:after{
    transform: rotate(60deg) translateX(calc(-2 * 3em));
    transform: rotate(60deg) translateX(calc(-2 * var(--height)));
}

.AlternativeSuperButton_btn__3Iu-0:before{
    transform: rotate(-60deg) translateX(calc(2 * 3em));
    transform: rotate(-60deg) translateX(calc(2 * var(--height)));
    transition-delay: 0.1s;
}

.AlternativeSuperButton_btn__3Iu-0:hover:after,
.AlternativeSuperButton_btn__3Iu-0:hover:before{
    content: '';
    position: absolute;
    top: 0px; left: 0px;
    transition: all ease-in-out .2s;
    height: calc(1 * 3em);
    height: calc(1 * var(--height));
    width: calc(2 * 0.866 * 3em);
    width: calc(2 * 0.866 * var(--height));
    background: rgb(64, 64, 64);
    background: var(--bgr);
    opacity: 1;
    z-index: -5;
}

.AlternativeSuperButton_btn__3Iu-0:hover:after{
    transform: rotate(60deg) translateX(0);

}

.AlternativeSuperButton_btn__3Iu-0:hover:before{
    transform:  rotateZ(-60deg) translateX(0px);
    transition-delay: 0.1s;
}

.AlternativeSuperButton_btn__3Iu-0:active:before,
.AlternativeSuperButton_btn__3Iu-0:active:after{
    transform: rotateZ(0deg);
}

.AlternativeSuperButton_btn__3Iu-0:active:before{
    transition-delay: .1s;
}
